import config from '../../components/config/config.vue'
import send from '../../components/my/sender.vue'
import company from '../../components/my/company.vue'
export default {
	components:{
		send,
		company
	},
	onLoad() {

	},
	mounted() {
		//加载用户信息
		this.onloaduser()
		//加载用户类别菜单
		this.onloaddiv()
		
	},
	data() {
		return {
			userimg:"../../static/nickname.png",
			nickname:"Nil",
			style_send:"",
			style_company:"",
			data:{
			        selectnum:0,
			    },				              
			List: [
			        {name:"0",value:"发货人"},
			        {name:"1",value:"货运公司"},
			    ],
			}
	},
	methods: {
		pickerChange: function(e,val) {				
		    console.log('picker发送选择改变，携带值为',e.target.value)
		    this.data[val]=e.target.value
			var strval= e.target.value+""
			//更新到本地
			uni.setStorageSync('selectnum',strval )
			if(strval=="0"){
				this.style_send=""
				this.style_company="display:none;"
			}else{
				this.style_company=""
				this.style_send="display:none;"
			}

		},
		onloaduser(){
			  var value = uni.getStorageSync('token');
			  if(value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/user/me',
			  		method:"GET",
			  	    header: {
			  	       'content-type': 'application/x-www-form-urlencoded',
			  		   'Authorization' : value,
			  	    },
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
			  				this.nickname=jsondata.data.nickname
							if(jsondata.data.avatar){
								this.userimg=jsondata.data.avatar
							}
			  			}else{
			  				config.txtshow(jsondata.msg)				  				
			  			}								
			  	    },
			  		fail: () =>{  
			  			config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  	uni.navigateTo({
			      url: '../user/login'
			  });
			    
			}
		},
		onloaddiv(){
			 var selectnum = uni.getStorageSync('selectnum');
			 if(selectnum=="1"){
				this.data["selectnum"]=1
				this.style_company=""					
				this.style_send="display:none;"
			 }else{
				 this.data["selectnum"]=0
				this.style_send=""
				this.style_company="display:none;"				 
			 }
		},
		gouserspace(){
			uni.navigateTo({
			    url: '../user/userspace'
			});
		}
	},
	
}
